@import url(https://fonts.googleapis.com/css?family=Roboto+Mono:500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background:#323738}#drum-machine{margin:10px auto 0 auto;width:500px}#drum-machine #drum-pads{display:grid;grid-gap:10px;grid-template-columns:auto auto auto;background:#426157;border:6px solid #ffe600;padding:20px;margin-top:10px;text-align:center}#drum-machine #drum-pads .drum-pad{background:#fc6603;padding:40px;font-size:30px;font-family:'Roboto Mono', monospace;text-align:center;font-weight:900;border-radius:20px;box-shadow:10px 10px 5px -6px rgba(0,0,0,0.75);cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}#drum-machine #drum-pads .drum-pad:active{background:#323738;color:#426157;box-shadow:10px 10px 11px -11px rgba(0,0,0,0.75);-webkit-transform:translate(3px, 2px);transform:translate(3px, 2px)}#drum-machine #display{font-size:40px;font-family:'Roboto', sans-serif;text-align:center;color:#ffe600;font-weight:900}#drum-machine #settings{box-sizing:content-box !important}#drum-machine #settings .volPower{float:right;line-height:21px;font-weight:700;font-size:25px;background:#426157;color:#ffe600;padding:7px;border:2px solid #ffe600;width:196px}#drum-machine #settings .volPower span{float:right}#drum-machine #settings #volumeLabel #slider{-webkit-appearance:none;width:100%;height:15px;border-radius:5px;background:#426157;outline:none;opacity:0.7;-webkit-transition:.2s;-webkit-transition:opacity .2s;transition:opacity .2s}#drum-machine #settings #volumeLabel #slider:hover{opacity:1}#drum-machine #settings #volumeLabel #slider::-webkit-slider-thumb{-webkit-appearance:none;appearance:none;width:15px;height:15px;border-radius:50%;background:#ffe600;cursor:pointer}#drum-machine #settings #volumeLabel #slider::-moz-range-thumb{width:15px;height:15px;border-radius:50%;background:#ffe600;cursor:pointer}#drum-machine #settings #onOffLabel{box-sizing:content-box !important;display:inline-block}#drum-machine #settings #onOffLabel .onoffswitch{position:relative;width:90px;box-sizing:content-box !important;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none}#drum-machine #settings #onOffLabel .onoffswitch .onoffswitch-checkbox:checked+.onoffswitch-label .onoffswitch-inner{margin-left:0}#drum-machine #settings #onOffLabel .onoffswitch .onoffswitch-checkbox:checked+.onoffswitch-label .onoffswitch-switch{right:0px}#drum-machine #settings #onOffLabel .onoffswitch .onoffswitch-checkbox{display:none}#drum-machine #settings #onOffLabel .onoffswitch .onoffswitch-label{display:block;overflow:hidden;cursor:pointer;border:2px solid #ffe600;border-radius:20px}#drum-machine #settings #onOffLabel .onoffswitch .onoffswitch-label .onoffswitch-inner{display:block;width:200%;margin-left:-100%;-webkit-transition:margin 0.3s ease-in 0s;transition:margin 0.3s ease-in 0s}#drum-machine #settings #onOffLabel .onoffswitch .onoffswitch-label .onoffswitch-inner:before,#drum-machine #settings #onOffLabel .onoffswitch .onoffswitch-label .onoffswitch-inner:after{display:block;float:left;width:50%;height:30px;padding:0;line-height:30px;font-size:14px;color:white;font-weight:bold}#drum-machine #settings #onOffLabel .onoffswitch .onoffswitch-label .onoffswitch-inner:before{content:"OFF";padding-left:10px;background-color:#426157;color:#ffe600}#drum-machine #settings #onOffLabel .onoffswitch .onoffswitch-label .onoffswitch-inner:after{content:"ON";padding-right:10px;background-color:#426157;color:#ffe600;text-align:right}#drum-machine #settings #onOffLabel .onoffswitch .onoffswitch-label .onoffswitch-switch{display:block;width:18px;margin:6px;background:#ffe600;position:absolute;top:0;bottom:0;right:56px;border:2px solid #ffe600;border-radius:20px;-webkit-transition:all 0.3s ease-in 0s;transition:all 0.3s ease-in 0s;box-sizing:content-box}@media screen and (max-width: 510px){#drum-machine{width:100vw}#drum-machine #settings{padding:0 7px 0 7px}}@media screen and (max-width: 470px){#drum-machine #drum-pads{padding:10px}#drum-machine #drum-pads .drum-pad{padding:30px}#drum-machine #settings .volPower{width:200px}}@media screen and (max-width: 340px){#drum-machine #drum-pads{grid-gap:5px}#drum-machine #drum-pads .drum-pad{padding:20px}}

